export default {
	data() {
		return {
			searchValue:'',
			exist:true,
			productList:[],
			tabBars: [],
			tabIndex: 0,
			id:'424856479015406202',
			pageNum:''
		};
	},
	onLoad(options) {
		// this.id = options.id
		this.getClassifyData('424856479015406202')
		this.getProduct(1,'')
	},
	methods: {
		ontabtap(e) {
			this.tabIndex = e.target.dataset.current || e.currentTarget.dataset.current;
		},
		goDetail(id,type){
			uni.navigateTo({
				url: `/pages/health-product-detail/health-product-detail?id=${id}&type=${type}`,
			});
		},
		getClassifyData(id){
			this.$api.getClassify({
				communityId:id
			}).then(res=>{
				let arr = res.data
				if(res.status=="OK"){
					this.tabBars.push({
						id:'',
						label:'全部',
					})
					arr.map(item=>{
						this.tabBars.push({
							id:item.id,
							label:item.name,
						})
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		getProduct(pageNum,searchValue){
			this.$api.customerProductPage({
				name:searchValue,
				size:10,
				page:pageNum,
				communityId:this.id
			}).then(res=>{
				if(res.status=="OK"){
					res.list.map(item=>{
						this.productList.push({
							price:item.price/100,
							originalPrice:item.originPrice/100,
							name:item.name,
							pic:JSON.parse(item.mainPic)[0].url,
							id:item.id,
							code:item.code
						})
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		search(res){
			this.getProduct(1,res.value)
		}
		
	},
	filters: {
		toFixed2: function(value) {
			return value.toFixed(2);
		},
	}
	
}
